/* @jsx jsx */
import {jsx} from "@emotion/react"

import {Helmet} from "react-helmet"

import PageLayout from "../components/PageLayout";
import {spacing8} from "../styles";

import * as styles from "../styles/ReportIssues.styles";

const getPageFromUrlParams = (urlParams: string): string | undefined => {
    const params = urlParams.substring(1).split("&").map(param => {
        return param.split("=")
    });
    const pageParam = params.find(param => param[0] === "page");
    if (pageParam) return pageParam[1];
    return undefined;
}

const ReportIssue = ({location}) => {
    const page = getPageFromUrlParams(location.search);

    const content = <div>
        <h1>Report an Issue</h1>
        <p>Report any typo, mistakes, or incorrect information.</p>
        <form
            action="https://formspree.io/f/myyknpla"
            method="POST"
        >
            <div>
                <div><label css={styles.requiredLabel}>Page</label></div>
                <div>
                    <input
                        css={styles.input}
                        type="text"
                        name="page"
                        defaultValue={page}
                        required
                    />
                </div>
            </div>
            <div css={spacing8}/>
            <div>
                <div><label>Your email (optional)</label></div>
                <div>
                    <input
                        css={styles.input}
                        type="email"
                        name="email"
                    />
                </div>
            </div>
            <div css={spacing8}/>
            <div>
                <div><label css={styles.requiredLabel}>Message</label></div>
                <div>
                    <textarea
                        css={styles.textarea}
                        name="message"
                        required
                    />
                </div>
            </div>
            <div css={spacing8}/>
            <div>
                <button
                    css={styles.button}
                    type="submit"
                >
                    Send
                </button>
            </div>
        </form>
    </div>

    return (
        <div>
            <Helmet>
                <meta charSet="utf-8"/>
                <meta name="viewport" content="width=device-width, initial-scale=1"/>
                <title>Report an Issue | Arshi Annafi</title>
                <meta name="description"
                      content="Found any typo or incorrect information, use this page to let me know."/>
            </Helmet>
            <div>
                <PageLayout content={content}/>
            </div>
        </div>
    )
}

export default ReportIssue
